import { useState,useEffect } from "react";


export default function Total(){
const [data, setData] = useState();
const [sd,setSd] = useState(1);

const customerId = localStorage.getItem("customerId");

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/customer/${customerId}/questions`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setData(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});

if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)
    return(
        <div>
            <h1>Total Question Asked</h1>
            <table border = {"border"} cellPadding={'10px'} cellSpacing={'9px'}>
                <thead>
                  <tr>
                    <td>Asked Date</td>
                    <td>Question Asked</td>
                    <td>Admin Comment</td>
                 </tr>
                </thead>

                <tbody>
                    {
                        data.map((e,i)=>{
                            return <tr key={i}>
                                <td>{e.date}</td>
                                <td>{e.question}</td>
                                <td>{e.comment}</td>
                            </tr>
                        })
                    }
                </tbody>

            </table>
        </div>
    );
}