import { useState,useEffect } from "react";


export default function Applied(){
const [data, setData] = useState();
const [sd,setSd] = useState(1);

const customerId = localStorage.getItem("customerId");

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/customer/${customerId}/applied`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setData(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});


if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)
return (
        <div>
            <h1>Applied Policies</h1>
            <table border = {"border"} cellPadding={'9px'} cellSpacing={'5px'}>
                <thead>
               <tr>
                   <th>Applied Policy Id.</th>
                   <th>Policy</th>
                   <th>Status</th>
                   <th>Applied Date</th>
               </tr>
               </thead>
    
                {
                    data.map((e,i)=>{
    
                        return(
                            <tbody key = {i}>
                              <tr>
                                <td>{e.id}</td>
                                <td>{e.policy}</td>
                                <td>{e.status}</td>
                                <td>{e.date}</td>
                              </tr>
                            </tbody>
    
                        );
    
                    })
                }
    
            </table>
        </div>
    );
}