import { useState,useEffect } from "react";


export default function Questions(){
const [datar,setDatar] = useState();
const [warn, setWarn] = useState("");
const [sd,setSd] = useState(1);

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/admin/questions`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setDatar(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});



const handleSend =(e)=>{
    if(e.key !== "Enter") return;
    const id = e.target.name;
    const answer = e.target.value;
    if(answer.trim()==="")return;

    setWarn("Seending Answer...")

    fetch(`http://localhost:8080/admin/answer?questionid=${id}&comment=${answer}`)
    .then(e=>{if(e.status===200)setWarn("Operation Successfull"); else setWarn("Unsuccuessfull")})
    .catch(e=>{setWarn("Error see console for more info");console.log(e)});

    const nd = datar.array.forEach(element => {
        if(element.id===id)
                element.comment=answer;
        return element;
    });
    setDatar(nd);
    
}

//there is probleam in the comment text box you have to solve it but need some more coding i think you make them separate for accepting comment and for sendign it.
if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)

return(
    <div>
        <h1>Questions Asked By The Customers</h1>
        <h4 color="red">{warn}</h4>

        <table border={"border"} cellPadding={'12px'} cellSpacing={'10px'}>
            <thead>
                <tr>
                    <th>Date</th>
                    <th>Customer</th>
                    <th>Question</th>
                    <th>Comment</th>
                    <th>Modify</th>

                </tr>
            </thead>

            <tbody>
                    {
                        datar.map((e,i)=>{
                            return(
                                <tr key={i}>
                                    <td>{e.date}</td>
                                    <td>{e.customer_id}</td>
                                    <td>{e.question}</td>
                                    <td>{e.comment}</td>
                                    <td><input name={e.id} onKeyPress={handleSend} type={"text"}/></td>
                                </tr>
                            )
                        })
                    }
            </tbody>
        </table>
    </div>
);
}