import { useRef, useState } from "react";
import { useEffect } from "react";

export default function Availabel(){
    const [datar,setDatar] = useState();
    const [sd,setSd] = useState(1);
    const [warn,setWarn] = useState("");
    const [warn2,setWarn2] = useState("");
    const pname = useRef();
    const cat = useRef();
    const su = useRef();
    const pre = useRef();
    const ten = useRef();

    useEffect(()=>{        
        let isMounted = true;
        fetch(`http://localhost:8080/customer/available`)
            .then(data=>data.json())
            .then((data)=>{if(isMounted)setDatar(data)})
            .then(()=>setSd(3))
            .catch(e=>{setSd(2);console.log(e)});
    
        return ()=>{isMounted=false};
    });

    const handleDelete = (e)=>{
        const id = e.target.name;
        setWarn2("....")
        fetch(`http://localhost:8080/admin/deletepolicy?policyid=${id}`)
            .then(e=>{if(e.status===200)setWarn2("Operation Successfull"); else setWarn2("Unsuccuessfull")})
            .catch(e=>{setWarn2("Error see console for more info");console.log(e)});
        //here lay a delete request 
        const nd = datar.filter((e)=>e.id!==id)
        setDatar(nd);
    };
    
    const handleAdd=(e)=>{
        e.preventDefault();
        const name = pname.current.value;
        const catagory = cat.current.value;
        const premium = pre.current.value;
        const tenure = ten.current.value;

        if(name===""||catagory===""||premium===""||tenure===""){setWarn("one or more field are empty");return;}

        setWarn("Adding....")
        fetch(`http://localhost:8080/admin/addpolicy`,{method:"POST",body:JSON.stringify(name,catagory,premium,tenure)})
            .then(e=>{if(e.status===200)setWarn("Policy added");else setWarn("unsuccussfull")})
            .catch(e=>{setWarn("there is error check the console for more info");console.log(e)});

        setDatar([...datar,{id:-1,name,catagory,premium,tenure}]);
        pname.current.value="";
        cat.current.value="";
        su.current.value="";
        pre.current.value="";
        ten.current.value="";

    };
    
if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)   
    return (
        <div>
            <h1>All Policies</h1>
            <h4 color="red">{warn2}</h4>
            <table border = {"border"} cellPadding={'9px'} cellSpacing={'5px'}>
                <thead>
               <tr>
                   <th>Policy ID.</th>
                   <th>Policy Name</th>
                   <th>Catagory</th>
                   <th>Premium</th>
                   <th>Tenure</th>
                   <th>Remove</th>
               </tr>
               </thead>
    
                {
                    datar.map((e,i)=>{
    
                        return(
                            <tbody key = {i}>
                              <tr>
                                <td>{e.id}</td>
                                <td>{e.name}</td>
                                <td>{e.catagory}</td>
                                <td>{e.premium}</td>
                                <td>{e.tenure}</td>
                                <td><button onClick={handleDelete} name = {e.id}>DELETE</button></td>
                              </tr>
                            </tbody>
    
                        );
    
                    })
                }
    
            </table><br/><br/>
            <h2>Add Policy</h2>
            <form>
                <label>Policy Name:</label> <input ref = {pname} type={"text"}/><br/><br/>
                <label>Catagory:</label> <input ref = {cat} type={"text"}/><br/><br/>
                <label>Premium:</label> <input ref = {pre} type={"number"}/><br/><br/>
                <label>Tenure:</label> <input ref = {ten} type={"number"}/><br/><br/>
                <label>{warn}</label><br/>
                <button onClick={handleAdd} type={"submit"}>Add</button>
            </form>
            
        </div>
    );
    }