import { useState,useEffect } from "react";


export default function Policy(){
const [selected,setSelected] = useState(false);
const [data, setData] = useState();
const [sd,setSd] = useState(1);
const [policyD,setPolicyD] = useState();

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/customer/catagory`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setData(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});

const handlePolicy = (e)=>{
    const index = e.target.name;
    setPolicyD(data[index]);
    setSelected(true);
};

if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)
    return (
        <div>
            <h1>Policies Catagory</h1>
            {
                data.map((e,i)=>{
                    return(
                        <div key = {i}> 
                        <button key={i} onClick = {handlePolicy} name = {i}>{e.name}</button><br/><br/> 
                        </div>
                    );
                })
            }
            {
                (function(){
                    if(selected)
                        return <>
                            <h1>Policy : {policyD.name}</h1>
                            <h3>Price : {policyD.price} Birr</h3>
                            <h3>Duration : {policyD.duration} month</h3>
                            <h3>Description : {policyD.description}</h3>
                        </>
                })()
            }

        </div>
    );





}