import { useState,useEffect } from "react";

export default function Available(){
const [warn, setWarn] = useState("");
const [data, setData] = useState();
const [sd,setSd] = useState(1);

const customerId = localStorage.getItem("customerId");

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/customer/available`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setData(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});


const handleApply = (e)=>{
    const policy = e.target.name;
    setWarn("Appling........")
        fetch(`http://localhost:8080/customer/${customerId}/apply?policy=${policy}`) 
        .then(e=>{if(e.status===200)setWarn("Policy Applied Successfully"); else setWarn("Policy didin't applyed") })
        .catch((e)=>{setWarn("connection error, see console");console.log(e)});

}

if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)
return (
    <div>
        <h1>Available Policies</h1>
        <h4 color="red">{warn}</h4>
        <table border = {"border"} cellPadding={'9px'} cellSpacing={'5px'}>
            <thead>
           <tr>
               <th>Policy Id.</th>
               <th>Policy Name</th>
               <th>Catagory</th>
               <th>Premium</th>
               <th>Tenure</th>
               <th>Apply</th>
           </tr>
           </thead>

            {
                data.map((e,i)=>{

                    return(
                        <tbody key = {i}>
                          <tr>
                            <td>{e.id}</td>
                            <td>{e.name}</td>
                            <td>{e.catagory}</td>
                            <td>{e.premium}</td>
                            <td>{e.tenure}</td>
                            <td><button onClick={handleApply} name = {e.name}>Apply</button>  </td>
                          </tr>
                        </tbody>

                    );

                })
            }

        </table>
    </div>
);
}