import { useState } from "react";
import Available from "./Available";
import Ask from "./Ask"
import Applied from "./Applied"
import Policy from "./Policy";
import Total from "./Total";

export default function Customer({dataleg,logoutHandler}){
const [current, setCurrent]= useState("");

localStorage.setItem("customerId",dataleg.customerId)
localStorage.setItem("customer",dataleg.customer)

const handleAvp = ()=> {setCurrent("available");};
const handleAp = ()=> {setCurrent("applied")};
const handlePc = ()=> {setCurrent("policy")};
const handleTqa = ()=> {setCurrent("total")};
const handleAq = ()=> {setCurrent("ask")};
const handleLogout = ()=>{logoutHandler()} //pls solve the variable names.

return(
    <div>
    <h1>Health Insurance Managment System</h1>
    <h3>{dataleg.customer}({dataleg.customerId})(customer)</h3>
    <button onClick={handleAvp}>Available Policy</button>
    <button onClick={handleAp}>Applied Policy</button>
    <button onClick={handlePc}>Policy Catagory</button>
    <button onClick={handleTqa}>Total Question Asked</button>
    <button onClick={handleAq}>Ask Question</button>
    <button onClick={handleLogout}>Logout</button>
    
    {
    current===""?<></>:
    current==="available"?<Available/>:
    current==="applied"?<Applied />:
    current==="policy"?<Policy />:
    current==="total"?<Total />:
    <Ask data={dataleg}/>
    }

  
    
    </div>
);

}