import { useState,useEffect } from "react";



export default function Applied(){
const [datar,setDatar] = useState();
const [warn, setWarn] = useState("");
const [sd,setSd] = useState(1);

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/admin/appliedpolicies`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setDatar(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});

    const handleDelete = (e)=>{
        const id = e.target.name;
        setWarn("deleteing.....")
        fetch(`http://localhost:8080/admin/deleteappliedpolicy?policyid=${id}`)
            .then(e=>{if(e.status===200)setWarn("Operation Successfull"); else setWarn("Unsuccuessfull")})
            .catch(e=>{setWarn("Error see console for more info");console.log(e)});
        const nd = datar.filter((e)=>e.id!==id)
        setDatar(nd);
    }

    const handleApprove = (e)=>{
        const id = e.target.name;
        setWarn("approving.....")
        fetch(`http://localhost:8080/admin/approve?policyid=${id}`)
            .then(e=>{if(e.status===200)setWarn("Operation Successfull"); else setWarn("Unsuccuessfull")})
            .catch(e=>{setWarn("Error see console for more info");console.log(e)});

        const nd = datar.map((e,i)=>{
            if(e.id ===id)
                return ({...e,status:"Approved"})
            return e;
        })
        setDatar(nd);
    }

    const handleDisapprove = (e)=>{
        const id = e.target.name;
        setWarn("approving.....")
        fetch(`http://localhost:8080/admin/disapprove?policyid=${id}`)
            .then(e=>{if(e.status===200)setWarn("Operation Successfull"); else setWarn("Unsuccuessfull")})
            .catch(e=>{setWarn("Error see console for more info");console.log(e)});

        const nd = datar.map((e,i)=>{
            if(e.id===id)
                return ({...e,status:"Disapproved"})
            return e;
        })
        setDatar(nd);
        
    }

 if(sd===1)
     return <><p>loading...</p></>
 if(sd===2)
     return <p>There is error check console or try other operation</p>
 if(sd===3)
    return(
        <div>
            <h1>All Policies Applied For!</h1>
            <h4 color="red">{warn}</h4>
            <table border = {"border"} cellPadding={'9px'} cellSpacing={'5px'}>
                <thead>
                    <tr>
                        <th>Date</th>
                        <th>A.Policy ID.</th>
                        <th>Customer ID</th>
                        <th>Policy</th>
                        <th>Status</th>
                        <th>Approve</th>
                        <th>Disapprove</th>
                        <th>Remove</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        datar.map((e,i)=>{
                            return <tr key={i}>
                                <td>{e.date}</td>
                                <td>{e.id}</td>
                                <td>{e.customer_id}</td>
                                <td>{e.policy}</td>
                                <td>{e.status}</td>
                                <td><button onClick={handleApprove} name = {e.id}>Approve</button></td>
                                <td><button onClick={handleDisapprove} name = {e.id}>Disapprove</button></td>
                                <td><button onClick={handleDelete} name = {e.id}>Delete</button></td>
                            </tr>
                        })
                    }
                </tbody>
            </table>
        </div>
    );
}