import { useState,useEffect } from "react";


export default function Customers(){
const [sd,setSd] = useState(1);
const [datar,setDatar] = useState();
const [warn, setWarn] = useState("");

useEffect(()=>{        
    let isMounted = true;
    fetch(`http://localhost:8080/admin/customers`)
        .then(data=>data.json())
        .then((data)=>{if(isMounted)setDatar(data)})
        .then(()=>setSd(3))
        .catch(e=>{setSd(2);console.log(e)});

    return ()=>{isMounted=false};
});

const handleDelete = (e)=>{
    const id = e.target.name;
    setWarn("....");
    fetch(`http://localhost:8080/admin/delete?customerid=${id}`)
        .then(e=>{if(e.status===200)setWarn("Operation Successfull");else setWarn("cannot delete")})
        .catch((e)=>{console.log(e);setWarn("there is error check console for more information")});
    const nd = datar.filter((e)=>e.id!==id);
    setDatar(nd);

}

if(sd===1)
    return <><p>loading...</p></>
if(sd===2)
    return <p>There is error check console or try other operation</p>
if(sd===3)
return (
    <div>
        <h1>All Customers/USER Registerd To The System</h1>
        <h4 color="red">{warn}</h4>
        <table border = {"border"} cellSpacing={'12px'} cellPadding={'10px'}>
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Phone</th>
                    <th>Delete</th>
                </tr>
            </thead>
            <tbody>
                {
                  datar.map((e,i)=>{
                      return <tr key={i}>
                          <td>{e.fullname}</td>
                          <td>{e.email}</td>
                          <td>{e.phone}</td>
                          <td><button onClick={handleDelete} name = {e.id}>DELETE</button></td>
                      </tr>
                  })  
                }
            </tbody>
        </table>
        
        
    </div>
);
}